@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700&family=Inter:wght@300;400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    /* Cyberpunk Color System - HSL Format */
    --background: 240 22% 13%;
    --foreground: 187 20% 90%;
    
    /* Primary: Electric Cyan */
    --primary: 187 97% 49%;
    --primary-foreground: 240 22% 13%;
    --primary-glow: 187 97% 65%;
    
    /* Secondary: Deep Purple */
    --secondary: 275 77% 35%;
    --secondary-foreground: 187 20% 90%;
    
    /* Accent: Bright Teal */
    --accent: 160 100% 50%;
    --accent-foreground: 240 22% 13%;
    
    /* Muted */
    --muted: 240 15% 20%;
    --muted-foreground: 187 15% 60%;
    
    /* Card */
    --card: 240 20% 16%;
    --card-foreground: 187 20% 90%;
    
    /* Popover */
    --popover: 240 20% 16%;
    --popover-foreground: 187 20% 90%;
    
    /* Border */
    --border: 187 40% 30%;
    --input: 187 40% 25%;
    --ring: 187 97% 49%;
    
    /* Destructive */
    --destructive: 0 85% 60%;
    --destructive-foreground: 0 0% 98%;
    
    /* Success */
    --success: 142 76% 45%;
    --success-foreground: 0 0% 98%;
    
    /* Warning */
    --warning: 56 97% 52%;
    --warning-foreground: 240 22% 13%;
    
    /* Special Effects */
    --neon-pink: 330 100% 62%;
    --cyber-yellow: 56 97% 52%;
    
    /* Gradients */
    --gradient-primary: linear-gradient(135deg, hsl(187, 97%, 49%), hsl(160, 100%, 50%));
    --gradient-secondary: linear-gradient(135deg, hsl(275, 77%, 35%), hsl(330, 100%, 62%));
    --gradient-accent: linear-gradient(90deg, hsl(160, 100%, 50%), hsl(187, 97%, 49%), hsl(275, 77%, 35%));
    --gradient-glow: linear-gradient(135deg, hsl(187, 97%, 49%) 0%, hsl(187, 97%, 65%) 100%);
    
    /* Shadows */
    --shadow-glow: 0 0 20px hsl(187 97% 49% / 0.4);
    --shadow-glow-strong: 0 0 30px hsl(187 97% 49% / 0.6);
    --shadow-neon: 0 0 15px hsl(160 100% 50% / 0.5);
    --shadow-purple: 0 0 20px hsl(275 77% 35% / 0.4);
    --shadow-card: 0 4px 20px hsl(0 0% 0% / 0.3);
    
    /* Animation */
    --transition-smooth: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    --transition-bounce: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    
    /* Typography */
    --font-heading: 'Space Grotesk', sans-serif;
    --font-body: 'Inter', sans-serif;
    
    /* Radius */
    --radius: 0.75rem;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  
  body {
    @apply bg-background text-foreground;
    font-family: var(--font-body);
    font-feature-settings: 'rlig' 1, 'calt' 1;
  }
  
  h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-heading);
  }
}

@layer utilities {
  /* Custom Gradients */
  .bg-gradient-primary {
    background: var(--gradient-primary);
  }
  
  .bg-gradient-secondary {
    background: var(--gradient-secondary);
  }
  
  .bg-gradient-accent {
    background: var(--gradient-accent);
  }
  
  .bg-gradient-glow {
    background: var(--gradient-glow);
  }
  
  /* Text Gradients */
  .text-gradient-primary {
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .text-gradient-accent {
    background: var(--gradient-accent);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  /* Glow Effects */
  .glow {
    box-shadow: var(--shadow-glow);
  }
  
  .glow-strong {
    box-shadow: var(--shadow-glow-strong);
  }
  
  .glow-neon {
    box-shadow: var(--shadow-neon);
  }
  
  .glow-purple {
    box-shadow: var(--shadow-purple);
  }
  
  /* Glass Effect */
  .glass {
    background: hsl(var(--card) / 0.6);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    border: 1px solid hsl(var(--border) / 0.3);
  }
  
  /* Animations */
  .animate-glow {
    animation: glow 2s ease-in-out infinite alternate;
  }
  
  @keyframes glow {
    from {
      box-shadow: 0 0 10px hsl(187 97% 49% / 0.3);
    }
    to {
      box-shadow: 0 0 20px hsl(187 97% 49% / 0.6);
    }
  }
  
  .animate-pulse-slow {
    animation: pulse-slow 3s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }
  
  @keyframes pulse-slow {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.7;
    }
  }
  
  .animate-float {
    animation: float 3s ease-in-out infinite;
  }
  
  @keyframes float {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
  }
  
  /* Scrollbar */
  .custom-scrollbar::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  
  .custom-scrollbar::-webkit-scrollbar-track {
    background: hsl(var(--muted));
    border-radius: 4px;
  }
  
  .custom-scrollbar::-webkit-scrollbar-thumb {
    background: hsl(var(--primary) / 0.5);
    border-radius: 4px;
  }
  
  .custom-scrollbar::-webkit-scrollbar-thumb:hover {
    background: hsl(var(--primary));
  }
}

/* Particle Background */
.particles-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  pointer-events: none;
  opacity: 0.3;
}

/* Smooth transitions for all interactive elements */
button, a, input, textarea, select {
  transition: var(--transition-smooth);
}

/* Focus visible for accessibility */
*:focus-visible {
  outline: 2px solid hsl(var(--primary));
  outline-offset: 2px;
}

/* Selection */
::selection {
  background: hsl(var(--primary) / 0.3);
  color: hsl(var(--foreground));
}